<template>
	<view>
		<cu-custom bgColor="bg-gradual-blue" :isBack="true">
			<block slot="content">预约详情</block>
		</cu-custom>
		<view class="cu-bar bg-white solid-bottom margin-top">
			<view class="action">
				<text class="cuIcon-title text-orange"></text> 座位信息
			</view>
		</view>
		<view class="seatdiv">
			<view class="seattitle">
				{{title}}
			</view>
			<view class="content-detail">
				{{floor}}
			</view>
			<view class="content-detail">
				{{detail}}
			</view>
			<view class="seatyuyuelist">

			</view>
		</view>


		<view class="cu-bar bg-white solid-bottom margin-top">
			<view class="action">
				<text class="cuIcon-title text-orange"></text> 预约信息
			</view>
		</view>
		<view class="cu-form-group">
			<view class="title">开始日期</view>
			<picker mode="date" :value="startdate" :start="nowdate" :end="nextdate" @change="DatestartChange">
				<view class="picker">
					{{startdate}}
				</view>
			</picker>
		</view>
		<view class="cu-form-group">
			<view class="title">结束日期</view>
			<picker mode="date" :value="enddate" :start="nowdate" :end="nextdate" @change="DateendChange">
				<view class="picker">
					{{enddate}}
				</view>
			</picker>
		</view>
		<view class="cu-form-group">
			<view class="title">开始时间</view>
			<picker mode="time" :value="starttime" start="00:01" end="23:59" @change="TimestartChange">
				<view class="picker">
					{{starttime}}
				</view>
			</picker>
		</view>
		<view class="cu-form-group">
			<view class="title">结束时间</view>
			<picker mode="time" :value="endtime" start="00:01" end="23:59" @change="TimeendChange">
				<view class="picker">
					{{endtime}}
				</view>
			</picker>
		</view>

		<view class="butgroup margin-top">
			<button class="cu-btn bg-green margin-tb-sm lg" @tap="yuyuefun">预约</button>
		</view>

	</view>


</template>

<script>
	export default {
		onLoad(options) {
			this.floor = options.floor
			this.title = options.title
			this.detail = options.detail
			this.id = options.id
			this.userinfoObj = uni.getStorageSync('userinfo')
		},
		mounted() {
			// this.init()
			this.initdatetime()
		},
		data() {
			return {
				userinfoObj:{},
				id:'',
				title:'',
				detail:'',
				floor:'',
				starttime: '11:00',
				endtime: '12:00',
				startdate: '',
				enddate: '',
				seatlist: [],
				nowdate:'',
				nextdate:''
			};
		},
		methods: {
			// async init() {
			// 	let that = this
			// 	let msgobj = {}
			// 	let resdata = await new this.Request(this.Urls.m().queryfloor, msgobj).modepost()
			// 	console.log(resdata)
			// 	let arr = []
			// 	for (let i = 0; i < resdata.length; i++) {
			// 		arr.push(resdata[i].title)
			// 	}
			// 	that.tabNav = arr
			// 	that.query(that.tabNav[that.TabCur])

			// },
			// async query(floor) {
			// 	let msgobj = {
			// 		floor: floor
			// 	}
			// 	let resdata = await new this.Request(this.Urls.m().queryseatbyfloor, msgobj).modepost()
			// 	this.seatlist = resdata
			// },
			TimestartChange(e) {
				this.starttime = e.detail.value
			},
			TimeendChange(e) {
				this.endtime = e.detail.value
			},
			DatestartChange(e) {
				this.startdate = e.detail.value
			},
			DateendChange(e) {
				this.enddate = e.detail.value
			},
			//预约座位
			async yuyuefun(){
				let that = this
				let msgobj = {
					datetime:this.getnowdate() + " " + this.getnowtime(),
					floor:this.floor,
					seat:this.title,
					username:this.userinfoObj.userName,
					userid:this.userinfoObj.id,
					status:'预约中',
					starttime:this.starttime,
					endtime:this.endtime,
					startdate:this.startdate,
					enddate:this.enddate
				}
				let resdata = await new this.Request(this.Urls.m().addyuyue, msgobj).modepost()
				console.log(resdata)
				if(resdata == 'success'){
					uni.showToast({
						title: '预约成功',
						icon: 'success',
						//显示持续时间为 2秒
						duration: 2000
					})  
				}
			},
			initdatetime(){
				this.nowdate = this.getnowdate()
				this.nextdate = this.getNextDate(this.nowdate,1)
				this.startdate = this.getnowdate()
				this.enddate = this.getnowdate()
			},
			getnowdate() {
				var nowDate = new Date();
				var year = nowDate.getFullYear();
				var month = nowDate.getMonth() + 1 < 10 ? "0" + (nowDate.getMonth() + 1) : nowDate.getMonth() + 1;
				var day = nowDate.getDate() < 10 ? "0" + nowDate.getDate() : nowDate.getDate();
				var dateStr = year + "-" + month + "-" + day;
				return dateStr;
			},
			getnowtime() {
				let date = new Date()
				let hours = date.getHours()
				let minutes = date.getMinutes() < 10 ? ('0' + date.getMinutes()) : date.getMinutes()
				let seconds = date.getSeconds() < 10 ? ('0' + date.getSeconds()) : date.getSeconds()
				let time = hours + ':' + minutes + ':' + seconds
				return time
			},
			// date 代表指定的日期，格式：2018-09-27
			// day 传-1表始前一天，传1表始后一天
			// JS获取指定日期的前一天，后一天
			 getNextDate(date, day) { 
			　　var dd = new Date(date);
			　　dd.setDate(dd.getDate() + day);
			　　var y = dd.getFullYear();
			　　var m = dd.getMonth() + 1 < 10 ? "0" + (dd.getMonth() + 1) : dd.getMonth() + 1;
			　　var d = dd.getDate() < 10 ? "0" + dd.getDate() : dd.getDate();
			　　return y + "-" + m + "-" + d;
			}
			

		}
	}
</script>

<style scoped>
	.seatdiv{
		padding-left: 3%;
		padding-right: 3%;
		
	}
	
	.seattitle{
		text-align: center;
		font-size: 1.5em;
		font-weight: bold;
		margin-bottom: 5px;
	}
	.content-detail {
		width: 100%;
		word-wrap: break-word;
		word-break: break-all;
	}
	
	.butgroup {
		padding-left: 5%;
		padding-right: 5%;
		width: 100%;
		display: flex;
		justify-content: space-around;
		align-items: center;
	}

	.cu-btn {
		width: 43%;
	}
</style>